.contact-filter {
  li {
    white-space:nowrap;
    position: relative;
    a {
      overflow: hidden;
      padding-left: 32px;
      padding-right: 40px;
      text-transform: capitalize;
      .badge {
        font-size: 70%;
        font-weight: normal;
        border-radius: 0;
        position: absolute;
        top: 10px;
        right: 10px;
        background: lighten(@badge-bg, 20%);
        box-shadow: 4px 0 15px 15px #fff;
      }
      &:hover,
      &:focus {
        text-decoration: none;
        background-color: @nav-link-hover-bg;
        .badge {
          box-shadow: 4px 0 15px 15px @nav-link-hover-bg;
        }
      }
    } // a
  } // li
}

//КОНТАКТЫ
.company-users, 
.contact-users,
.contact-companys {
  &:extend(.list-results);
    @media (max-width: @screen-md-max) { 
      //&:extend(.btn-xs);
    }
  .contact {
    //position:relative;
    overflow: hidden;
    > a {
      position: absolute;
      top:0; left:0; right:0; bottom:0;
      z-index: 1;
      cursor: pointer;
    }
    .photo {
       width:60px; //80px
      img {
        &:extend(.img-circle, .img-responsive, .pull-left);
      }
      @media (max-width: @screen-md-max) { 
        width:80px;
      }
    } // /.photo
    .info {
      &:extend(.v-top);
      width:100%; 
      .clearfix > [class^="col-"] {
        white-space:nowrap;
      }
      span {
        &:extend(.text-sm);
      }
      .clearfix{
        margin: .5em 0;
      }
      h4,h5 {
        margin: 0;        
      }
    } // /.info

  } // /.contact

  .btn {
    &:extend(.ink-reaction, .btn-xs, .btn-primary-bright);
      &:nth-child(2) {
        &:extend(.btn-accent-bright);        
      }
    margin:.5em;
    margin-left:0;
  } // /.btn
  .status {
    &:extend(.stick-top-right, .force-padding);
    background: @white;
    .box-shadow(-13px 17px 25px 2px @white);
      &.online {
        &:extend(.text-success);
      }
      &.offline {
        &:extend(.text-danger);
      }
  } // /.status

  .contact:hover .status {
    background:@gray-lightest;
    .box-shadow(-13px 17px 25px 2px @gray-lightest);
  }
} // /.company-users

.contact-companys {
  .contact .photo {
    position: relative;
    img {
      border-radius: 0;
    }
    &:before {
      content:' ';
      position: absolute;
      display: block;
      width:100%;
      height:100%;
      //.box-shadow(inset 0 0 50px rgba(0, 0, 0, 0.1));
    }
  } // /.photo
}