// ALSO SEE -> /jade/modules/layouts/__mixins_zaya.jade

@color-orange: #ff9900;
@color-green: #48b04b;
@color-blue: #00ccff;
@color-purple: #6e4db0;
@color-gray: #5f7d8c;


.text-emphasis-variant(@color) {
  color: @color;
  a&:hover {
    color: darken(@color, 10%);
  }
}


.label-variant(@color) {
  color: darken(@color, 10%);
	background: lighten(@color, 45%);
  // a&:hover {
  //   color: darken(@color, 10%);
  // }
}



// ЗАКАЗЧИК -> метки
.text-customer_formir        { .text-emphasis-variant(@color-orange); }
.text-customer_inwork        { .text-emphasis-variant(@color-green); }
.text-customer_agree         { .text-emphasis-variant(@color-blue); }
.text-customer_lp            { .text-emphasis-variant(@color-purple); }
.text-customer_arch          { .text-emphasis-variant(@color-gray); }

.label-customer_formir        { .label-variant(@color-orange); }
.label-customer_inwork        { .label-variant(@color-green); }
.label-customer_agree         { .label-variant(@color-blue); }
.label-customer_lp            { .label-variant(@color-purple); }
.label-customer_arch          { .label-variant(@color-gray); }

// ЗАКАЗЧИК -> прогресс-бары
.progress-bar-customer_formir { .progress-bar-variant(@color-orange);  width: 5%;    }
.progress-bar-customer_inwork { .progress-bar-variant(@color-green);   width: 50%;   }
.progress-bar-customer_agree  { .progress-bar-variant(@color-blue);    width: 75%;   }
.progress-bar-customer_lp     { .progress-bar-variant(@color-purple);  width: 85%;   }
.progress-bar-customer_arch   { .progress-bar-variant(@color-gray);    width: 100%;  }

// ЗАКАЗЧИК -> окно
.card-variant(modal-zaya_customer_formir,  @color-orange);
.card-variant(modal-zaya_customer_inwork,  @color-green);
.card-variant(modal-zaya_customer_agree,   @color-blue);
.card-variant(modal-zaya_customer_lp,      @color-purple);
.card-variant(modal-zaya_customer_arch,    @color-gray);


