
.modal-body.modal_profile_card {
  min-height: 300px;
} //.modal-body 

.profile-card {
  .row {
    margin-bottom:2em;
    .actions{
      &:extend(.stick-top-right,.text-success,.force-padding);
      .box-shadow(-5px 5px 20px 15px @white);
      background:@white;
      a {
        &:extend(.btn, .ink-reaction, .btn-xs);
        margin-right:1em;
        margin-bottom:1em;
        display:block;
        text-decoration:none;
        .transition(transform 320ms cubic-bezier(0.17, 0.67, 0.6, 1.3));      
        &:active {
          .transform(scale(.8));
        }  
      }
    }
  }
  .photo {
    &:extend(.pull-left, .width-3, .clearfix, .text-center);
    margin-top: 1em;

    img {
      &:extend(.img-circle, .size-2);
    }

  }

  h1 {
    &:extend(.text-light);
    font-size:185%;
  }
  h6 {
    &:extend(.text-light);
  }

  dl {
    min-height: 60px;
    &:extend(.dl-horizontal.dl-icon,.text-lg);
    dt span {
      &:extend(.text-medium,.opacity-25);
    }
    dd {
      line-height: 1;
    }
    dd {
      span  {      
        margin-right: .5em;
        &:nth-child(1),
        &.title {
          &:extend(.opacity-25, .text-medium);
          margin-bottom: .5em;
          display: block;
        }
      }
      span, a {
        &:extend(.text-medium);
        line-height: 1!important;
      }
    }

  }  
} //.profile-card